<template>
    <div class="search">
        <div class="searchBox">
            <i class="iconfont icon-search"></i>
            <input autofocus @keydown="getList(text)" type="text" v-model="text">
            <i v-show="text" @click.stop="close" class="iconfont icon-2guanbi"></i>
        </div>
        <div v-if="!text" class="hot">
            <p class="title">热门搜索</p>
            <ul class="list">
                <li @click="search(item.first)" class="item" ref="item" v-for="(item,i) in hot" :key="i">
                    <span>{{item.first}}</span>
                </li>
            </ul>
        </div>
        <div class="wrapper" v-else>
            <my-scroll class="scroll">
                <div>
                    <ul ref="searchList" class="searchList">
                        <li @click="play(item.id)" v-for="(item,i) in searchList" :key=i>
                            <div class="song">
                                <div class="name">
                                    <span>{{item.name.slice(0,15)}}</span>
                                </div>
                                <div class="author">
                                    <span>{{item.artists[0].name}}</span>&nbsp;-
                                    <span>{{item.album.name}}</span>
                                </div>
                            </div>
                            <i class="iconfont icon-iconset0481"></i>
                        </li>
                    </ul>
                </div>
            </my-scroll>
        </div>
    </div>
</template>

<script>
    import { getHot,getKeyword } from '@/api/search'
    import myScroll from '@/base/scroll/scroll'
    export default {
        data(){
            return {
                text: '',
                hot: [],
                searchList: []
            }
        },
        computed:{
            
        },
        methods:{
            getList(key){
                getKeyword(key).then(res => {
                    if(res.data.code === 200){
                        this.searchList = res.data.result.songs
                    }
                })
            },
            play(id){
                this.$router.push({path: 'song',query:{id:id}})
            },
            search(text){
                this.text = text
                this.getList(this.text)
            },
            close(){
                this.text = ''
                this.searchList = []
            }
        },
        created(){
            getHot().then(res => {
                this.hot = res.data.result.hots                
            })
        },
        components:{
            myScroll
        },
    }
</script>
<style lang="less" scoped>
    @import "../common/style/variable.less";
    .search {
        position: relative;
        .searchBox {
            width: 100%;
            height: 60px;
            position: relative;
            input {
                padding-left: 30px;
                display: block;
                width: 90%;
                height: 35px;
                line-height: 20px;
                font-size: @font-size-xl;
                margin: 10px auto;
                border-radius: 20px;
                background: #e2d8d8;
                outline: none;
            }
            .icon-search {
                position: absolute;
                left: 12px;
                top: 8px;
                font-size: @font-size-xxl;
            }
            .icon-2guanbi {
                position: absolute;
                right: 12px;
                top: 8px;
                font-size: @font-size-xxl;
            }
        }
        .wrapper {
            height: 470px;
            overflow: hidden;
            position: absolute;
            left: 0;
            top: 60px;
            .scroll {
                height: 100%;
                overflow: hidden;
                .searchList {
                    padding: 0 10px;
                    display: flex;
                    flex-wrap: wrap;
                    li {
                        width: 100%;
                        height: 40px;
                        flex: 1 1 auto;
                        padding-left: 10px;
                        padding-top: 10px;
                        position: relative;
                        display: flex;
                        .rank {
                            height: 100%;
                            line-height: 40px;
                            width: 30px;
                            text-align: center;
                        }
                        .song {
                            padding-left: 5px;
                            .name {
                                span {
                                    font-size: @font-size-xxl;
                                }
                            }
                            .author {
                                padding-top: 5px;         
                                span {
                                    color: @color-text-g;
                                    font-size: @font-size-m;
                                }
                            }
                        }
                        .iconfont {
                            position: absolute;
                            right: 10px;
                            top: 5px;
                            font-size: @font-size-big;
                        }
                    }
                }
            }
        }
        
        .hot {
            padding: 0 10px;
            .title {
                height: 20px;
                font-size: @font-size-l;
            }
            .list {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .item {
                    height: 30px;
                    line-height: 30px;
                    margin: 5px;
                    padding: 0 5px;
                    border: 1px solid #e7e6e6;
                    border-radius: 30px;
                    font-size: @font-size-l;
                }
            }
        }
    }
</style>